.grid_container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  margin-top: 1.5rem;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  row-gap: 20px;
}

@media screen and (max-width: 1000px) {
  .grid_container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    margin-top: 1.5rem;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    gap: 20px;
  }
}
@media screen and (max-width: 500px) {
  .grid_container {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    margin-top: 1.5rem;
    margin-left: auto;
    margin-right: auto;
    /* width: 80%; */
    gap: 20px;
  }
}

